import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewTruck from '@/components/NewTruck'
import ViewTruck from '@/components/ViewTruck'
import EditTruck from '@/components/EditTruck'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-truck',
      component: NewTruck
    },
    {
      path: '/:truckID',
      name: 'view-truck',
      component: ViewTruck
    },
    {
      path: '/edit/:truckID',
      name: 'edit-truck',
      component: EditTruck
    }
  ]
})



