<template>
  <div id="new-employee">
    <h3>Edit Truck</h3>
    <div class="row">
    <form @submit.prevent="updateTruck" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckID" required style="text-transform:uppercase">
          <label class="active">Truck ID</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckVin" required style="text-transform:uppercase">
          <label for="VIN" class="active">VIN</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckUnit" required style="text-transform:uppercase">
          <label for="truckUnit" class="active">Unit</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckModel" required style="text-transform:uppercase">
          <label for="truckModel" class="active">Model</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckYear" required>
          <label for="truckYear" class="active">Year</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckLoc" required style="text-transform:uppercase">
          <label for="truckLoc" class="active">Location</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="keyLoc" required>
          <label for="keyLoc" class="active">Key Location</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckAvailable" required style="text-transform:uppercase">
          <label for="truckAvailable" class="active">Available</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="clean" required style="text-transform:uppercase">
          <label for="Clean" class="active">Clean</label>
        </div>
      </div>
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
    </form>
  </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'edit-truck',
    data () {
      return {
        truckID: null,
        clean: null,
        keyLoc: null,
        truckAvailable: null,
        truckLoc: null,
        truckModel: null,
        truckUnit: null,
        truckVin: null,
        truckYear: null
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('trucks').where('truckID', '==', to.params.truckID).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.truckID = doc.data().truckID
            vm.clean = doc.data().clean
            vm.keyLoc = doc.data().keyLoc
            vm.truckAvailable = doc.data().truckAvailable
            vm.truckLoc = doc.data().truckLoc
            vm.truckModel = doc.data().truckModel
            vm.truckUnit = doc.data().truckUnit
            vm.truckVin = doc.data().truckVin
            vm.truckYear = doc.data().truckYear
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('trucks').where('truckID', '==', this.$route.params.truckID).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.truckID = doc.data().truckID
          })
        })
      },
      updateTruck () {
        db.collection('trucks').where('truckID', '==', this.$route.params.truckID).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref.update({
              truckID: this.truckID,
              truckVin: this.truckVin,
              truckUnit: this.truckUnit,
              truckModel: this.truckModel,
              truckYear: this.truckYear,
              truckLoc: this.truckLoc,
              keyLoc: this.keyLoc,
              truckAvailable: this.truckAvailable,
              clean: this.clean
      })
      .then(() => {
        this.$router.push({ name: 'view-truck', params: { truckID: this.truckID }})
      });
    })
  })
}
    }
  }
</script>

<style lang="css">
</style>



