<template lang="html">
  <div id="view-truck">
    <ul class="collection with-header" style="text-transform:uppercase">
      <li class="collection-header"><h4>{{truckUnit}}</h4></li>
      <li class="collection-item">Truck ID#: {{truckID}}</li>
      <li class="collection-item">Clean: {{clean}}</li>
      <li class="collection-item">Key Location: {{keyLoc}}</li>
      <li class="collection-item">Available: {{truckAvailable}}</li>
      <li class="collection-item">Truck Location: {{truckLoc}}</li>
      <li class="collection-item">Model: {{truckModel}}</li>
      <li class="collection-item">VIN: {{truckVin}}</li>
      <li class="collection-item">Year: {{truckYear}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteTruck" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>

    <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-truck', params: { truckID: truckID }}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>

</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'view-truck',
    data () {
      return {
        truckID: null,
        clean: null,
        keyLoc: null,
        truckAvailable: null,
        truckLoc: null,
        truckModel: null,
        truckUnit: null,
        truckVin: null,
        truckYear: null
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('trucks').where('truckID', '==', to.params.truckID).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.truckID = doc.data().truckID
            vm.clean = doc.data().clean
            vm.keyLoc = doc.data().keyLoc
            vm.truckAvailable = doc.data().truckAvailable
            vm.truckLoc = doc.data().truckLoc
            vm.truckModel = doc.data().truckModel
            vm.truckUnit = doc.data().truckUnit
            vm.truckVin = doc.data().truckVin
            vm.truckYear = doc.data().truckYear
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('trucks').where('truckID', '==', this.$route.params.truckID).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.truckID = doc.data().truckID
          })
        })
      },
      deleteTruck () {
        if(confirm ('Are you sure?')) {
          db.collection('trucks').where('truckID', '==', this.$route.params.truckID).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push('/')
            })
          })
        }
      }
    }
  }
</script>

<style lang="css">
</style>



