<template lang="html">
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Trucks</h4>

      </li>
      <table>
        <tr>
          <th></th>
          <th>Available</th>
          <th>Clean</th>
          <th>Unit</th>
          <th>Key</th>
          <th>Year</th>
          <th>Model</th>
          <th>Location</th>
          <th></th>
        </tr>
        <tr v-for="trucks in trucks" :key="trucks.truckID" class="collection-item" style="text-transform:uppercase">
          <td>
            <i class="material-icons">label</i>
          </td>
          <td>
            {{trucks.truckAvailable}}
          </td>
          <td>
            {{trucks.clean}}
          </td>
          <td>
            {{trucks.truckUnit}}
          </td>
          <td>
            {{trucks.keyLoc}}
          </td>
          <td>
            {{trucks.truckYear}}
          </td>
          <td>
            {{trucks.truckModel}}
          </td>
          <td>
            {{trucks.truckLoc}}
          </td>
          <td>
            <router-link class="secondary-content" v-bind:to="{ name: 'edit-truck', params: {truckID: trucks.truckID }}">
              <i class="material-icons">edit</i>
            </router-link>
            <router-link class="secondary-content" v-bind:to="{ name: 'view-truck', params: {truckID: trucks.truckID}}">
              <i class="material-icons">pageview</i>
            </router-link>
          </td>
        </tr>
      </table>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      trucks: []
    }
  },
  created () {
    db.collection('trucks').orderBy("truckAvailable", "desc").get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.data())
        const data = {
          'clean': doc.data().clean,
          'keyLoc': doc.data().keyLoc,
          'truckAvailable': doc.data().truckAvailable,
          'truckID': doc.data().truckID,
          'truckLoc': doc.data().truckLoc,
          'truckModel': doc.data().truckModel,
          'truckUnit': doc.data().truckUnit,
          'truckVin': doc.data().truckVin,
          'truckYear': doc.data().truckYear
        }
        this.trucks.push(data)
      })
    })
  }
}
</script>

<style lang="css">
</style>



