<template>
  <div id="new-truck">
    <h3>New Truck</h3>
    <div class="row">
    <form @submit.prevent="saveTruck" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckID" required style="text-transform:uppercase">
          <label>truck ID#</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckVin" required style="text-transform:uppercase">
          <label>VIN</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckUnit" required style="text-transform:uppercase">
          <label>Unit</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckModel" required style="text-transform:uppercase">
          <label>Model</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckYear" required>
          <label>Year</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckLoc" required style="text-transform:uppercase">
          <label>Location</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="keyLoc" required>
          <label>Key Location</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="truckAvailable" required style="text-transform:uppercase">
          <label>Available</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="clean" required style="text-transform:uppercase">
          <label>Clean</label>
        </div>
      </div>
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
    </form>
  </div>
  </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
      name: 'new-truck',
      data () {
        return {
          truckID: null,
          truckVin: null,
          truckUnit: null,
          truckModel: null,
          truckYear: null,
          truckLoc: null,
          keyLoc: null,
          truckAvailable: null,
          clean: null
        }
      },
      methods: {
        saveTruck () {
          db.collection('trucks').add({
            truckID: this.truckID,
            truckVin: this.truckVin,
            truckUnit: this.truckUnit,
            truckModel: this.truckModel,
            truckYear: this.truckYear,
            truckLoc: this.truckLoc,
            keyLoc: this.keyLoc,
            truckAvailable: this.truckAvailable,
            clean: this.clean
          })
          .then(docRef => {
            console.log('Truck added: ', docRef.id)
            this.$router.push('/')
          })
          .catch(error => {
            console.error('Error adding truck: ', error)
          })
        }
      }
    }
</script>

<style lang="css">
</style>



